@tailwind base;
@tailwind components;
@tailwind utilities;

/* --- GLOBAL DARK BACKGROUND --- */
body {
  font-family: sans-serif;
  margin: 0;
  background-color: #121212 !important; /* Force dark mode */
  color: #e0e0e0;
}

/* --- WIDER CONTAINER FOR FILTERS ETC --- */
.container {
  max-width: 1600px;
  margin: auto;
  padding: 1rem;
}

/* --- HEADER --- */
h1 {
  text-align: center;
  font-size: 2rem;
  margin-bottom: 1rem;
  color: #ffffff;
}

/* --- FORM & FILTER PANEL --- */
form,
.filter-panel {
  background: #1e1e1e;
  padding: 1rem;
  border-radius: 10px;
  box-shadow: 0 0 5px rgba(255, 255, 255, 0.05);
  margin-bottom: 1rem;
}

/* --- INPUTS & BUTTONS --- */
input,
select,
button {
  width: auto;
  min-width: 120px;
  flex: 1;
  padding: 0.4rem;
  margin-top: 0.15rem;
  border-radius: 6px;
  border: 1px solid #444;
  font-size: 1rem;
  background-color: #2c2c2c;
  color: #e0e0e0;
  transition: background-color 0.2s, border-color 0.2s;
}

input,
select {
  background-color: #2c2c2c;
  border-color: #555;
  color: #ddd;
}

input:focus,
select:focus,
button:focus {
  outline: none;
  border-color: #3b82f6;
  box-shadow: 0 0 5px 1px #3b82f6;
}

button {
  background-color: #3b82f6;
  color: white;
  border: none;
  cursor: pointer;
  transition: background 0.2s;
}

button:hover {
  background-color: #2563eb;
}

/* --- CARD GALLERY LAYOUT: FULL WIDTH, CARDS CENTERED, NO GAP PROBLEM --- */
.card-gallery {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fit, 240px);
  gap: 1.2rem;
  margin-bottom: 2rem;
  justify-content: center;
}

/* --- CARD BASE --- */
.card {
  background-color: #1e1e1e;
  border-radius: 16px;
  width: 240px;
  height: 580px;
  box-shadow: 0 4px 18px rgba(0, 0, 0, 0.35);
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0;
  padding: 20px 10px 16px 10px;
  box-sizing: border-box;
  position: relative;
  overflow: hidden;
  border: 2.5px solid transparent; /* Prevent layout shift, actual color set by rarity/unowned */
  background-clip: padding-box;
}

/* --- RARITY BORDERS & BACKGROUNDS --- */
.card-common {
  background: #dcd6dd;
  border: 2.5px solid #d1d5db;
}
.card-rare {
  background: #fbe9ef;
  border: 2.5px solid #f9a8d4;
}
.card-epic {
  background: #cce6f4;
  border: 2.5px solid #60a5fa;
}
.card-legendary {
  background: linear-gradient(135deg, #373231 0%, #ffe985 100%);
  box-shadow: 0 0 22px 4px #ffd700bb, 0 0 0 2px #ffd700cc;
  border: 2.5px solid #ffd700;
}
.card-mystic {
  background: linear-gradient(135deg, #2d1a38 0%, #e0b3ff 100%);
  box-shadow: 0 0 28px 4px #a663e4aa, 0 0 0 2px #c084fcdd;
  border: 2.5px solid #c084fc;
}
.card-celestial {
  background: linear-gradient(135deg, #101f2e 0%, #1e293b 50%, #38bdf8 100%);
  box-shadow: 0 0 32px 8px #38bdf8cc;
  border: 2.5px solid #38bdf8;
}

/* --- ONLY Wild/Unowned with NO RARITY --- */
.card.unowned:not(.card-common):not(.card-rare):not(.card-epic):not(
    .card-legendary
  ):not(.card-mystic):not(.card-celestial) {
  border: 2.5px dashed #94a3b8;
  background: #1e293b;
  box-shadow: none;
}

/* --- CARD IMAGE --- */
.card img {
  width: 90%;
  height: 345px;
  max-height: 345px;
  object-fit: cover;
  border-radius: 12px;
  display: block;
  margin: 0 auto 12px auto;
  background: #222;
}

.card h3 {
  margin: 0.5rem 0 0.1rem 0;
  font-family: "Fira Code", monospace;
  color: #222;
  font-size: 1.15rem;
  letter-spacing: 1px;
}

.card .tags {
  font-size: 0.92rem;
  margin-top: 0.28rem;
  margin-bottom: 0.2rem;
  color: #222;
  word-break: break-word;
  max-height: 36px;
  overflow-y: auto;
  text-align: center;
}

.card p,
.card .tags {
  min-height: 22px;
  text-align: center;
  width: 90%;
}

.card p {
  font-size: 0.96rem;
  color: #222;
  margin: 0.1rem auto 0.18rem auto;
  word-break: break-word;
  max-width: 200px;
  overflow-wrap: break-word;
}

/* --- BUTTON BAR --- */
.card .actions {
  margin-top: auto;
  display: flex;
  width: 100%;
  gap: 0.7rem;
  justify-content: center;
}

.card .actions button {
  flex: 1 1 0%;
  min-width: 0;
  padding: 0.45rem 0;
  font-size: 0.92rem;
  border-radius: 7px;
  max-width: 110px;
}

/* --- RARITY TEXT COLORS --- */
.card-common p,
.card-common h3,
.card-common .tags,
.card-rare p,
.card-rare h3,
.card-rare .tags,
.card-epic p,
.card-epic h3,
.card-epic .tags {
  color: #222 !important;
}
.card-legendary p,
.card-legendary h3,
.card-legendary .tags {
  color: #ffe04c !important;
}
.card-mystic p,
.card-mystic h3,
.card-mystic .tags {
  color: #ecd6fa !important;
}
.card-celestial p,
.card-celestial h3,
.card-celestial .tags {
  color: #ffffff !important;
}

/* --- MODALS (Unchanged) --- */
.modal,
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.85);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999;
}
.modal-content {
  position: relative;
  background: #1e1e1e;
  padding: 1.4rem 1.2rem 1.6rem 1.2rem;
  border-radius: 14px;
  max-width: 98vw;
  max-height: 92vh;
  overflow: auto;
  box-shadow: 0 0 15px rgba(255, 255, 255, 0.1);
}
.modal-content img {
  max-width: 100%;
  max-height: 80vh;
  display: block;
  margin: 0 auto 10px auto;
  border-radius: 8px;
}
.close-btn {
  margin-top: 1rem;
  background: #ef4444;
  color: white;
  border: none;
  padding: 0.4rem 1.4rem;
  font-size: 1rem;
  border-radius: 6px;
  cursor: pointer;
  display: block;
  margin-left: auto;
}
.close-btn:hover {
  background: #dc2626;
}

.card-gallery + h2 {
  margin-top: 2rem;
}
